















import { Component, Prop, Vue } from 'vue-property-decorator';

import Slick from 'vue-slick';
import { BigSliderItem } from '@/models/models';

@Component({
  components: {
    Slick,
  },
})
export default class BigSlider extends Vue {
  @Prop() public items!: BigSliderItem[];

  @Prop() public promo!: BigSliderItem[];

  get promoImage(): BigSliderItem | null {
    return this.promo && this.promo.length ? this.promo[0] : null;
  }

  sliderSettings = {
    slidesToShow: 1,
    swipeToSlide: true,
    mobileFirst: true,
    dots: true,
    arrows: false,
    centerMode: true,
    centerPadding: '15px',
    responsive: [
      {
        breakpoint: '300',
        settings: {
          dots: true,
        },
      },
      {
        breakpoint: '1023',
        settings: {
          dots: true,
          centerMode: false,
          arrows: true,
        },
      },
    ],
  };
}
